<template>
  <div :class="['haunted-house', { 'haunted-house-purple-bg': purpleBg }]">
    <div v-if="showHide" class="house-hide-header"></div>
    <img v-if="ghost === 'women'" class='house-women-ghost' src='@/assets/images/women-ghost.png' alt='women-ghost'>
    <img v-else-if="ghost === 'ling'" class='house-ling-ghost' src='@/assets/images/ling-photo.png' alt='ling'>
    <!-- QA 1 -->
    <div v-if="questionNum === 1" class="house-qa">
      <div class="house-question-box">
        <div class="house-question-head">選擇回答</div>
        <div class="house-question-text">我很害怕，你可以不要抓我嗎？</div>
      </div>
      <button @click="nextOne" class="house-btn">好，我不抓你。</button>
      <router-link to="/haunted-house-fight">
        <button class="house-btn">我抓爆你。</button>
      </router-link>
    </div>
    <!-- QA 2 -->
    <div v-if="questionNum === 2" class="house-qa">
      <div class="house-question-box">
        <div class="house-question-head">選擇回答</div>
        <div class="house-question-text">謝謝你不抓我，那我要獻出原型了。</div>
      </div>
      <button @click="nextTwo" class="house-btn">挖，是大叔耶！</button>
      <router-link to="/haunted-house-fight">
        <button class="house-btn">你長得好奇怪。</button>
      </router-link>
    </div>
    <!-- QA 3 -->
    <div v-if="questionNum === 3" class="house-qa">
      <div class="house-question-box">
        <div class="house-question-head">搭配實體道具找出答案</div>
        <div class="house-question-text">五個兄弟，住在一起，名字不同，高矮不齊？</div>
        <div class="house-input-head">請輸入答案</div>
        <div class="house-input-box">
          <input v-model.trim="answer" class="house-input" type="text">
        </div>
        <div class="house-confirm-box">
        <img @click="confirmAnswer" class='house-confirm' src='@/assets/images/go.png' alt='go'>
        </div>
      </div>
    </div>
    <!-- finish Ling 4-->
    <div @click="getCard" v-if="questionNum === 4" class="house-finish">
      <img class='house-ling-card' src='@/assets/images/ling-card-purple.png' alt='ling-card-purple'>
      <div class="house-finish-head">獲得林阿群</div>
      <div>林阿群，從以前就在基隆長大.....</div>
    </div>
    <!-- last shadow 5-->
    <div v-if="questionNum === 5" class="house-qa">
      <div class="house-question-box">
        <div class="house-question-head">選擇回答</div>
        <div class="house-question-text">我還有最後一個請求...</div>
      </div>
      <button @click="buyCard" class="house-btn">我幫你！</button>
      <router-link to="/monster-compass">
        <button class="house-btn">趕時間，走人。</button>
      </router-link>
    </div>
    <!-- buy card 6-->
    <div v-if="questionNum === 6" class="house-qa">
      <div class="house-question-box">
        <div class="house-question-head">出示此畫面</div>
        <div class="house-question-text">你可以花費 15 元，向見書店購買限量林開群實體閃卡。</div>
        <router-link to="/monster-compass">
          <button class="house-btn">完成任務，回到羅盤。</button>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>

export default {
  name: 'HauntedHouse',
  components: {

  },
  data () {
    return {
      questionNum: 1,
      ghost: 'women',
      answer: '',
      purpleBg: false,
      showHide: true
    }
  },
  mounted () {
  },
  methods: {
    nextOne () {
      this.questionNum = 2
      this.ghost = 'ling'
    },
    nextTwo () {
      this.questionNum = 3
    },
    confirmAnswer () {
      if (this.answer === '手指' || this.answer === '腳趾' || this.answer === '腳指' || this.answer === '手' || this.answer === '腳') {
        alert('恭喜答對了！')
        this.questionNum = 4
        this.ghost = ''
      } else {
        alert('請再想想唷！')
      }
    },
    getCard () {
      this.questionNum = 5
      this.ghost = 'ling'
      this.purpleBg = true
      this.showHide = false
      // 晃動 header
      this.$store.commit('shakeHeader', 'collect')
      setTimeout(() => {
        this.$store.commit('shakeHeader', '')
      }, 1000)
      // 取得卡片
      this.$store.commit('getLingCard', 'purpleLingCard')
    },
    buyCard () {
      this.questionNum = 6
    }
  }
}
</script>
<style lang="scss" scoped>

  .haunted-house {
    min-height: 100vh;
    padding-top: 100px;
    background: linear-gradient(#000 70%, #95ff00);
    display: flex;
    flex-direction: column;
    align-items: center;

    &-purple-bg {
      background: linear-gradient( #a785b7 70%, #95ff00);
    }

    .house {

      &-hide-header {
        width: 100%;
        height: 100px;
        position: fixed;
        top: 0px;
        left: 0px;
        background: rgba(0, 0, 0, 0.8);
        z-index: 2;
      }

      &-women-ghost {
        width: 150px;
        position: fixed;
      }

      &-ling-ghost {
        width: 320px;
        position: fixed;
        top: 0px;
      }

      &-qa {
        margin-top: 200px;
        z-index: 1;
      }

      &-question-box {
        padding: 20px;
        background-color: rgba(149, 255, 0, 0.8);
        border-radius: 20px;
        margin: 0px 40px 20px;
      }

      &-question-head {
        color: #ff0000;
      }

      &-question-text {
        margin-top: 10px;
      }

      &-btn {
        width: 100%;
        height: 40px;
        margin-top: 10px;
        color: #000;
        border-radius: 40px;
        background: #fff;
      }

      &-input-head {
        text-align: center;
        margin-top: 20px;
      }

      &-input-box {
        text-align: center;
        margin-top: 4px;
      }

      &-input {
        padding: 6px;
      }

      &-confirm-box {
        text-align: center;
        margin-top: 20px;
      }

      &-confirm {
        width: 40px;
      }

      // 取得林卡片

      &-finish {
        text-align: center;
        color: #fff;
      }

      &-ling-card {
        width: 300px;
      }

      &-finish-head {
        font-size: 24px;
        margin: 4px 0px;
      }
    }
  }

</style>



